/* eslint-disable no-extend-native */
/* eslint-disable no-var */
/* eslint-disable func-names */
String.prototype.includes = function (str) {
  var returnValue = false;

  if (this.indexOf(str) !== -1) {
    returnValue = true;
  }

  return returnValue;
};

const ampCa = {};

const isStaging = (url) => {
  if (url.includes('localhost') || url.includes('dev') || url.includes('stg') || url.includes('stage')) {
    return true;
  }
  return false;
};

ampCa.getContentEnv = () => {
  if (isStaging(window.location.hostname)) {
    return '9c2ecb6j86lt1gm3hx01m1tfs.staging.bigcontent.io';
  }
  return 'c1.adis.ws';
};

ampCa.getComponentEnv = () => {
  if (isStaging(window.location.hostname)) {
    return 'stage';
  }
  return 'prod';
};

if (window && typeof window !== 'undefined') {
  window.ampCa = ampCa;
}



